.matchItemWrapper{width:100%;display:flex;align-items:center;justify-content:space-between;gap:var(--m12);background-color:var(--BlackLite);padding:var(--inboxPaddingSm) var(--inboxPaddingLg);border-radius:var(--borderSm)}.matchItemWrapper .name{display:flex;align-items:center;gap:var(--m14);font-weight:600;color:var(--White);font-family:var(--font-inter);font-size:var(--p16);line-height:var(--lineHeightXS)}.matchItemWrapper .name svg{width:48px;height:48px}.matchItemWrapper .score{display:flex;flex-direction:column;align-items:center;gap:4px;font-size:var(--p16);font-weight:600;color:var(--White);font-family:var(--font-inter);font-size:var(--p20)}.matchItemWrapper .score .status{text-align:center;min-width:92px;border-radius:var(--borderSm);padding:var(--m6);font-family:var(--font-inter);font-weight:600;font-size:var(--p12);line-height:var(--lineHeightXS)}.matchItemWrapper .score .status.finished{background-color:var(--Red)}.matchItemWrapper .score .status.scheduled{background-color:var(--Orange)}.matchItemWrapper .score .status.ongoing{background-color:var(--Green)}@media only screen and (max-width: 767px){.matchItemWrapper{flex-wrap:wrap}.matchItemWrapper .name{gap:var(--m8);width:calc(50% - var(--m12));line-height:var(--lineHeightM)}.matchItemWrapper .name:first-child{order:1}.matchItemWrapper .name:last-child{order:2}.matchItemWrapper .name svg{width:30px;height:30px}.matchItemWrapper .score{width:100%;order:3}}